import * as React from "react";
import { useState } from "react";
import { FaCaretDown } from "react-icons/fa";

const Dropdown = ({ pages }) => {
  const [isDropdownOpen, setDropdown] = useState(false);

  const toggleDropwdown = () => {
    setDropdown(!isDropdownOpen);
  };

  const renderList = (pages) => {
    return (
      <ul className="list-none w-28 py-2">
        {pages.map((page) => (
          <li>
            <a
              className="block w-max mb-1 mx-4 py-2 
                         text-center rounded-md 
                         hover:bg-gray-300 transition-transform"
              href={page.link}
            >
              {page.label}
            </a>
          </li>
        ))}
      </ul>
    );
  };

  return (
    <>
      <div className="w-max">
        <div className="relative">
          <div>
            <button
              onClick={toggleDropwdown}
              className="mx-2 mt-1 p-2 
                         text-center rounded-md 
                         hover:bg-gray-300 focus:outline-none"
            >
              <FaCaretDown />
            </button>
          </div>
          <div className="fixed right-0 mt-1 bg-gray-100 rounded-b-md drop-shadow-lg">
            {isDropdownOpen ? renderList(pages) : null}
          </div>
        </div>
      </div>
    </>
  );
};

export default Dropdown;
