import * as React from "react";
import Dropdown from "./dropdown";

const Navbar = () => {
  const pages = [
    { label: "JSX", link: "/" },
    { label: "Props", link: "/" },
    { label: "States", link: "/" },
    { label: "Hooks", link: "/" }
  ];

  return (
    <>
      <nav className="bg-gray-100 py-1 w-full drop-shadow-lg">
        <div className="flex justify-between">
          <div className="w-1/4">
            <a
              className="p-2 inline-block align-middle font-medium text-blue-600"
              href="/"
            >
              Learn React
            </a>
          </div>
          <div className="hidden sm:flex w-1/2 max-w-lg items-center">
            {pages.map((page) => (
              <a
                className="mx-1 p-2 w-1/4 
                           text-center rounded-md 
                           hover:bg-gray-300 transition-transform"
                href={page.link}
              >
                {page.label}
              </a>
            ))}
          </div>
          <div className="sm:hidden">
            <Dropdown pages={pages} />
          </div>
        </div>
      </nav>
    </>
  );
};

export default Navbar;
